import React from 'react';
import '../modules/Card.module.css';

export default function Card({max, min, name, onClose, img}) {
  // acá va tu código
  return (
  <div>    
    <button className="closeBtn" onClick={onClose}>X</button>
    <h1>{name}</h1>
    <h1>Temperatura Máxima: {max}</h1>
    <h1>Temperatura Mínima: {min}</h1>
    <img src={`http://openweathermap.org/img/wn/${img}@2x.png`} alt="Clima imagen"/>
  </div>
  )
};